.popup {
    position: fixed;
    z-index: 2;
    top: 0;
    bottom: 0;
    right: 0;
    left: 0;
    width: 100%;
    height: 100%;
    visibility: hidden;
    opacity: 0;
    transition:visibility .5s linear,  opacity .5s linear ;
    
    background-color: rgba(0, 0, 0, 0.5);
}


